@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper{display:flex}.item{flex:1 1 200px;order:2}body{font-family:"Playfair Display",Tahoma,Geneva,Verdana,sans-serif;font-size:18px;color:#1f271b;background-color:#f6ffff;margin:0}a{text-decoration:none}.link:hover{transition:all .35s ease;-webkit-transition:all .35s ease;cursor:pointer}.link{transition:all .35s linear;-webkit-transition:all .35s linear;cursor:pointer}button{padding:16px;font-family:Calibri,"Trebuchet MS",sans-serif;font-size:16px;background-color:#000;color:#f3fcf0;border-radius:8px;cursor:pointer}h3,h4{margin-bottom:0;margin-top:0}nav{text-align:center;position:-webkit-sticky;position:sticky;top:0;width:100%;background-color:#f6ffff;fill-opacity:#f6ffff;box-shadow:1rem -0.7rem 1rem .1rem}nav ul{list-style-type:none;margin:0;padding:0}nav li{display:inline-block;padding-top:16px;padding-bottom:16px}nav a{text-decoration:none;color:#1f271b;font-weight:bold;padding:16px 32px}nav a:hover{text-decoration:none;background:#1f271b;color:#f3fcf0}#page-body{max-width:700px;margin:auto;padding-top:1rem}h1,h2{font-weight:600;color:#1f271b}.article-list-item{text-decoration:none;color:#1f271b;padding:2rem 1rem 2rem 1rem;font-family:Calibri,"Trebuchet MS",sans-serif;border-bottom:.1rem solid #1f271b}.article-list-item:hover{border-radius:.3rem;box-shadow:1px 1px 10px -7px #88292f,1px 1px 1px -7px #88292f;border-bottom:.1rem solid #88292f;opacity:.85}.article-list-item h3{color:#000;text-decoration:none;border-bottom:none}.article-list-item p{color:gray;margin-bottom:0}#other-articles{padding:.5rem 1rem;margin-top:2rem;background-color:#dbd4d3;border-top-left-radius:1rem;border-top-right-radius:1rem}#other-articles h2{color:#88292f}.article-page{color:#000}.article-page #article-title{color:#1f271b}#upvotes-section{display:flex;flex-flow:row no-wrap;justify-content:space-evenly;align-items:center;font-family:Calibri,"Trebuchet MS",sans-serif}#upvotes-section span{display:flex;flex-flow:row wrap;justify-content:space-around;align-items:center;margin:1rem;padding-top:1rem;padding-bottom:1rem}#upvotes-section .upvote-btn{color:#1f271b;font-size:2.5rem;padding:1rem}#upvotes-section .upvote-btn:hover{font-size:3rem}#upvotes-section #upvote-info{color:#a47b88;font-weight:800}.comments-list{margin:1rem 0 3rem 0}.comments-list h2{font-family:"Playfair Display",Tahoma,Geneva,Verdana,sans-serif;margin-bottom:2.5rem}.comments-list .comment-container{display:flex;flex-flow:row nowrap;justify-content:flex-start;align-items:flex-start;font-family:Calibri,"Trebuchet MS",sans-serif;border-radius:.2rem;border-bottom:.1rem solid transparent;margin-top:1rem;padding:1.5rem;box-shadow:1px 1px 200px -2.5rem #dbd4d3}.comments-list .comment-container #right{padding-left:1rem}.comments-list .comment-container:hover{transition:all .5s ease;border-bottom:.1rem solid #88292f}.comments-list #no-comments{margin-top:3rem;margin-bottom:3rem;font-family:Calibri,"Trebuchet MS",sans-serif;text-align:center;color:#88292f;font-weight:600}#add-comment-form{border-bottom:2px solid #1f271b;padding:1rem}#add-comment-form h3{margin-bottom:2rem;font-family:"Playfair Display",Tahoma,Geneva,Verdana,sans-serif}#add-comment-form #name{width:40%}#add-comment-form textarea{width:100%}#add-comment-form input,#add-comment-form textarea{font-family:Calibri,"Trebuchet MS",sans-serif;margin-bottom:1rem;padding:.5rem;border-radius:.2rem;font-size:1rem;background-color:#dbd4d3;word-wrap:break-word;resize:none}#add-comment-form input:focus,#add-comment-form textarea:focus{border:.2rem solid #bfcde0;border-bottom:.2rem solid #88292f;outline:none;font-size:1.1rem}#add-comment-form button{font-weight:bolder;color:#dbd4d3;margin:0;padding:.5rem;padding-left:1.5rem;padding-right:1.5rem;text-align:center;border-radius:.2rem}#add-comment-form button:hover{background-color:#88292f}
